import React from 'react'
import Lottie from 'lottie-react'
import Contactus from "./cnt.json"

export default function ContactPage() {
   
    const showcontactpage=()=>{
        var contactpage=document.getElementById("contactpageid")
        return contactpage.classList.add("active")
      }
      const hidecontactpage=()=>{
        var contactpage=document.getElementById("contactpageid")
        return contactpage.classList.remove("active")
     }
     const inputCity=document.getElementById("inputCity")
     const mobile=document.getElementById("mobile")
     const name=document.getElementById("name")
     function Checksubmitfnc(e){
     if(mobile.value.length!=10 ){
      alert("Please check the mobile number")
      e.preventDefault();
    }
   
  }    

  return (<>
   <div className='contactusanimation'  style={{position:"fixed",zIndex:"3",top:"40vh",cursor:"pointer",display:"flex"}}>
    <Lottie style={{display:"flex",overflow:"visible",left:"-100px"}} onClick={showcontactpage} animationData={Contactus} />
</div>

<div id='contactpageid' className='contactpage fontclass container' data-bs-theme="dark" style={{display:"flex",flexWrap:"wrap",alignItems:"center",justifyContent:"end"}} ><button className="btn btn-close btn-" style={{float:'right',padding:"10px",color:"red"}} onClick={hidecontactpage}></button>
<form className="row g-3" action="https://formspree.io/f/mgejobpa"
  method="POST" style={{paddingLeft:"30px",paddingRight:"30px",paddingBottom:"50px"}} >
    <div style={{textAlign:"center"}}><h1>Weâ€™d love to hear from you! Reach out to us for all your queries.</h1></div>
    <div style={{textAlign:"left"}}><h1></h1></div>
  <div className="col-sm-12 col-md-6  ">
    <label for="name"  className="form-label">Name</label>
    <input type="text" id='name' className="form-control" name="Name" />
  </div>
  <div className="col-sm-12 col-md-6  ">
    <label for="inputEmail4" className="form-label">Email</label>
    <input type="email" className="form-control" name="Email" id="inputEmail4"/>
  </div>
  <div className="col-12 col-md-6 ">
    <label for="mobile" className="form-label">Mobile No.</label>
    <input type="number" id="mobile" className="form-control" name="Mobile" />
  </div>
  <div className="col-12 col-md-6 ">
    <label for="inputAddress" className="form-label">Address</label>
    <input type="text" className="form-control" name="Address1" id="inputAddress" placeholder="H.no, street no"/>
  </div>
  <div className="col-12 col-md-6 ">
    <label for="inputAddress2" className="form-label">Address 2</label>
    <input type="text" className="form-control" name="Address2" id="inputAddress2" placeholder="Area , city"/>
  </div>
  <div className="col-12 col-md-6 ">
    <label for="inputCity" className="form-label">City</label>
    <input type="text" className="form-control" name="City" id="inputCity"/>
  </div>
  
  <div className="col-12 ">
    <input type='text' className='form-control'  placeholder='Enter your requirements' name='Requirements'></input>
  </div>
 
  <div className="btncnt" style={{display:"flex",justifyContent:"center"}}>

    <div className='submitbtn'>
    <button type="submit" className="btn" onClick={Checksubmitfnc}>Submit</button>
    </div>
  </div>
</form>
</div> 
    
  </>
  )
}
